<template>
    <section class="main_cont">
        <!--  -->
        <el-row class="toolbar bdr_radiu f-cb">
            <el-form :inline="true" status-icon :model="filterData" ref="filterOrderForm" label-width="40px">
                <el-form-item label="">
                    <el-date-picker
                        v-model="filterData.timeArray"
                        type="daterange"
                        align="right"
                        unlink-panels
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        format="yyyy-MM-dd"
                        @change="getSTime"
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="target" class="filter_text">
                    <el-input v-model="filterData.searchText" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="filterOrderSubmit('filterOrderForm')"></el-button>
                    <el-button @click="resetForm('filterOrderForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-row>

        <!--  -->
        <el-row>
            <el-table class="order_list" :data="order_info" border highlight-current-row v-loading="listLoading" height="calc(100vh - 250px)">
                <!--<el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->

                <el-table-column label="" width="120" align="center">
                    <template slot-scope="scope">
      		<img :src="scope.row.product.img" class="tabimg" />
      	</template>
                </el-table-column>

                <el-table-column prop="product.title" label="" width="200" align="center"></el-table-column>
                <el-table-column prop="id" label="" width="80" align="center"></el-table-column>
                <el-table-column prop="finalPrice" label="" width="80" align="center"></el-table-column>
                <el-table-column prop="createAt" label="" width="110" align="center"></el-table-column>
                <el-table-column prop="userMobile" label="" width="120" align="center"></el-table-column>
                <el-table-column prop="payAt" label="" width="110" align="center"></el-table-column>
                <el-table-column prop="status" label="" width="auto" :formatter="formatStatusType"></el-table-column>
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar f-cb">
                <el-pagination class="f-fr pagination"
                   :current-page.sync='page_arg.page_index'
                   :page-sizes="page_arg.page_sizes"
                   :page-size="page_arg.page_size"
                   :layout="page_arg.layout"
                   :total="page_arg.total"
                   @current-change='handleCurrentChange'
                   @size-change='handleSizeChange'>
                </el-pagination>
            </el-col>
        </el-row>
    </section>
</template>

<script>
    import {
        findOrderList,  // 
    } from '../../api/api.js';

    export default {
        name: 'order',

        data() {
            return {
                /**
                 * common
                 */
                listLoading: false,  // lodding

                // 
                page_arg: {
                    page_index: 1, // 
                    total: 0, // 
                    page_size: 10, // 1
                    page_sizes: [5, 10, 15, 20, 50], //
                    layout: "total, sizes, prev, pager, next, jumper" // 
                },

                // 
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },

                // 
                filterData: {
                    timeArray: '',  // 
                    startTime: "",  // 
                    endTime: "",  // 
                    searchText: ""  // 
                },

                /**
                 * 
                 */
                order_info: [],  // 
            }
        },
        methods: {
            /**
             * common
             */
            // 
            handleCurrentChange() {
                // 
                this.getOrderList();
            },
            // 
            handleSizeChange(page_size) {
                // console.log(page_size);

                // size
                this.page_arg.page_size = page_size;

                // 
                this.getOrderList();
            },

            // 
            resetForm(formName) {
                this.$refs[formName].resetFields();

                this.filterData.searchText = "";
                this.filterData.timeArray = "";

                // 
                this.getOrderList();
            },

            /**
             *  
             */
            // 
            getSTime(val) {
                if(val){
                    // 
                    this.filterData.startTime = val[0];
                    this.filterData.endTime = val[1];
                }

                return false;
            },

            /**
             *  api
             *  
             */
            // 
            getOrderList () {
                // loading
                this.listLoading = true;

                // 
                let param = {
                    pageNum: this.page_arg.page_index,  // 
                    pageSize: this.page_arg.page_size,  // 
                    startTime: this.filterData.startTime,  // yyyy-MM-dd
                    endTime: this.filterData.endTime,  // yyyy-MM-dd
                    searchText: this.filterData.searchText,  // 
                };

                // 
                findOrderList(param).then(res => {
                    // console.log(res);

                    this.order_info = res.data.data.list;

                    // 
                    this.page_arg.total = res.data.data.total;

                    // loading
                    this.listLoading = false;
                }).catch({});
            },
            // 
            formatStatusType (row) {
                switch (row.type) {
                    case 0:
                        return "";
                        break;
                    case 1:
                        return "";
                        break;
                    case -1:
                        return "";
                        break;
                }
            },
            // 
            filterOrderSubmit (formName) {
                // 
                this.$refs[formName].validate((valid) => {
                    // 
                    if (valid) {
                        // 
                        this.getOrderList();
                    } else {  //
                        console.log('error submit!!');
                    }
                });
            }
        },
        // 
        created () {
            // 
            this.getOrderList();
        }
    }
</script>

<style scoped>
    .toolbar{
        padding-left: 28px;
    }

    .filter_text{
        width: 280px;
    }

    .pagination{
        margin-bottom: 5px;
    }
</style>