<template>
    <section class="main_cont">
        <!--  -->
        <el-row>
            <el-form :inline="true">
                <el-col class="toolbar bdr_radiu" :span="24">
                    <el-col :span="22">
                        <el-form-item>
                            <el-button type="primary" @click="addBanner" v-on:click="addBannerVisible = true">banner</el-button>
                        </el-form-item>
                    </el-col>
                </el-col>
            </el-form>
        </el-row>

        <!-- banner -->
        <el-row>
            <el-table class="banner_list" :data="banner_info" border highlight-current-row v-loading="listLoading" height="calc(100vh - 242px)">
                <!--<el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->
                <el-table-column prop="id" label="banner_id" width="100" align="center"></el-table-column>
                <el-table-column prop="title" label="" width="100" align="center"></el-table-column>

                <el-table-column label="banner" width="100" align="center">
                    <template slot-scope="scope">
      		<img :src="scope.row.img" class="tabimg" />
      	</template>
                </el-table-column>

                <el-table-column label="" width="100" align="center" :formatter="formatType"></el-table-column>

                <el-table-column label="" width="100" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <a :href="scope.row.target" target="_blank" class="buttonText"></a>
                    </template>
                </el-table-column>

                <el-table-column prop="desc" label="" width="auto"></el-table-column>

                <el-table-column fixed="right" label="" width="100">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="updateBanner(scope.row)" v-on:click="updateBannerVisible = true"></el-button>
                        <el-button type="text" size="small" @click="delBanner(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar f-cb">
                <el-pagination class="f-fr pagination"
                   :current-page.sync='page_arg.page_index'
                   :page-sizes="page_arg.page_sizes"
                   :page-size="page_arg.page_size"
                   :layout="page_arg.layout"
                   :total="page_arg.total"
                   @current-change='handleCurrentChange'
                   @size-change='handleSizeChange'>
                </el-pagination>
            </el-col>
        </el-row>

        <!-- banner -->
        <el-dialog title="banner" @keyup.enter.native="addBannerSubmit('addBannerForm')" :close-on-click-modal="false" :visible.sync="addBannerVisible" :before-close="handleClose">
            <el-form :model="addBannerData" status-icon :rules="addBannerRules" ref="addBannerForm" label-width="120px">
                <el-form-item label="" prop="target">
                    <el-input v-model="addBannerData.target"  placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="banner" prop="title">
                    <el-input v-model="addBannerData.title" placeholder="banner" clearable></el-input>
                </el-form-item>
                <el-form-item label="banner" prop="type">
                    <el-input v-model="addBannerData.type" placeholder="banner" clearable></el-input>
                    <ul class="tip">
                        <li>0</li>
                        <li>1</li>
                        <li>2: URL</li>
                    </ul>
                </el-form-item>

                <el-form-item label="banner">
                    <el-upload
                        action="https://jsonplaceholder.typicode.com/posts/"
                        ref='upload'
                        :before-remove="beforeRemove"
                        :on-change="addHandleChange"
                        multiple
                        :limit="upload_arg.limit"
                        :on-exceed="handleExceed"
                        :file-list="upload_arg.fileList">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                </el-form-item>

                <el-form-item label="banner" prop="img">
                    <el-input v-model="addBannerData.img" placeholder="banner" clearable></el-input>
                </el-form-item>
                <el-form-item label="banner" prop="desc">
                    <el-input type="textarea" v-model="addBannerData.desc" placeholder="banner" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="addBannerSubmit('addBannerForm')"></el-button>
                    <el-button @click="resetForm('addBannerForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!-- banner -->
        <el-dialog title="banner" @keyup.enter.native="updateBannerSubmit('updateBannerForm')" :close-on-click-modal="false" :visible.sync="updateBannerVisible" :before-close="handleClose">
            <el-form :model="updateBannerData" status-icon :rules="updateBannerRules" ref="updateBannerForm" label-width="110px">
                <el-form-item label="bannerId" prop="id">
                    <el-input v-model="updateBannerData.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="target">
                    <el-input v-model="updateBannerData.target"  placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="banner" prop="title">
                    <el-input v-model="updateBannerData.title" placeholder="banner" clearable></el-input>
                </el-form-item>
                <el-form-item label="banner" prop="type">
                    <el-input v-model="updateBannerData.type" placeholder="banner" clearable></el-input>
                    <ul class="tip">
                        <li>0</li>
                        <li>1</li>
                        <li>2: URL</li>
                    </ul>
                </el-form-item>

                <el-form-item label="banner">
                    <el-upload
                            action="https://jsonplaceholder.typicode.com/posts/"
                            ref='upload'
                            :before-remove="beforeRemove"
                            :on-change="updateHandleChange"
                            multiple
                            :limit="upload_arg.limit"
                            :on-exceed="handleExceed"
                            :file-list="upload_arg.fileList">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                </el-form-item>

                <el-form-item label="banner" prop="img">
                    <el-input v-model="updateBannerData.img" placeholder="banner" clearable></el-input>
                </el-form-item>
                <el-form-item label="banner" prop="desc">
                    <el-input type="textarea" v-model="updateBannerData.desc" placeholder="banner" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="updateBannerSubmit('updateBannerForm')"></el-button>
                    <el-button @click="resetForm('updateBannerForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import {
        findBannerList,  // 
        getImgURI,  // 
        addBanner,  // banner
        updateBanner,  // banner
        delBanner  // banner
    } from '../../api/api.js';

    export default {
        name: 'banner',

        data() {
            // banner
            let validateType = (rule, value, callback) => {
                let reg = /^[0-2]$/;

                if (!reg.test(value)) {
                    return callback(new Error('banner0-2'));
                }

                callback();
            };

            // url
            let validateUrl = (rule, value, callback) => {
                let reg = /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&amp;:/~\+#]*[\w\-\@?^=%&amp;/~\+#])?/;

                if (!reg.test(value)) {
                    return callback(new Error('url'));
                }

                callback();
            };

            return {
                /**
                 * common
                 */
                listLoading: false,  // lodding
                dialogVisible: false,  // 

                // 
                page_arg: {
                    page_index: 1, // 
                    total: 0, // 
                    page_size: 10, // 1
                    page_sizes: [5, 10, 15, 20, 50], //
                    layout: "total, sizes, prev, pager, next, jumper" // 
                },

                // 
                upload_arg: {
                    limit:1,
                    imgFile: [],
                    fileList: []
                },

                /**
                 * banner
                 */
                banner_info: [],  // 

                /**
                 * banner
                 */
                // banner
                addBannerData: {
                    target: "http://",  // 
                    type: "0",  //  01, 2: URL,  NULL 
                    title: "",  // 
                    img: "https://",  // 
                    desc: "",  // 
                },

                // 
                addBannerRules: {
                    target: [
                        { validator: validateUrl, trigger: 'blur' }
                    ],
                    type: [
                        { validator: validateType, trigger: 'blur' }
                    ],
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    img: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateUrl, trigger: 'blur' }
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },

                /**
                 * banner
                 */
                // banner
                updateBannerData: {
                    id: "",
                    bannerId: "",
                    target: "http://",  // 
                    type: "",  //  01, 2: URL,  NULL 
                    title: "",  // 
                    img: "https://",  // 
                    desc: "",  // 
                },

                // 
                updateBannerRules: {
                    target: [
                        { validator: validateUrl, trigger: 'blur' }
                    ],
                    type: [
                        { validator: validateType, trigger: 'blur' }
                    ],
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    img: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateUrl, trigger: 'blur' }
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },

                /**
                 *  (true , false )
                 */
                addBannerVisible: false,  // banner
                updateBannerVisible: false,  // banner
            }
        },
        methods: {
            /**
             * common
             */
            // 
            handleClose(done) {
                this.$confirm('').then(() => {
                    done();
                }).catch(() => {});
            },
            // 
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //
            getFileType(fileName){
                let fileLength = fileName.length,  //
                    beforeFileLength = fileName.lastIndexOf('.');  //

                //
                let suffix = fileName.substring(beforeFileLength+1, fileLength);

                return suffix;
            },

            /**
             *  
             */
            // 
            handleCurrentChange() {
                this.getBannerList();  // 
            },
            // 
            handleSizeChange(page_size) {
                // console.log(page_size);

                // size
                this.page_arg.page_size = page_size;

                // 
                this.getBannerList();
            },

            /**
             * el-upload common
             */
            // 
            handleExceed(files, fileList) {
                this.$message.warning(` ${this.upload_arg.limit}  ${files.length}  ${files.length + fileList.length} `);
            },
            // 
            beforeRemove(file) {
                this.addBannerData.img = "";
                this.updateBannerData.img = "";
                return this.$confirm(` ${ file.name }`);
            },

            /**
             *  api
             *  banner
             */
            getBannerList () {
                //
                let param = {
                    pageNum: this.page_arg.page_index,  // 
                    pageSize: this.page_arg.page_size,  // 
                };

                // 
                findBannerList(qs.stringify(param)).then(res => {
                    // console.log(res.data.data);

                    this.banner_info = res.data.data.list;

                    // 
                    this.page_arg.total = res.data.data.total;
                }).catch({});
            },
            // banner
            formatType (row) {
                switch (row.type) {
                    case 0:
                        return "";
                        break;
                    case 1:
                        return "";
                        break;
                    case 2:
                        return "url";
                        break;
                    default :
                        return "null";
                        break;
                }
            },

            /**
             *  api
             *  banner
             */
            //banner
            addBanner () {
                // console.log("banner");

                this.upload_arg.fileList = [];  //img file
            },
            //el-upload
            // 
            addHandleChange(file){
                // console.log("");

                //pushfiles
                this.upload_arg.imgFile.push(file.raw);

                // console.log(this.upload_arg.imgFile);

                //
                this.addUpload();
            },

            // img url
            addUpload(){
                let formData = new FormData();

                formData.append('logoFile', this.upload_arg.imgFile[0]);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                getImgURI(formData, config).then(res => {
                    // console.log(res);

                    this.addBannerData.img = res.data.data.imgUrl;
                });
            },
            // 
            addBannerSubmit (formName) {
                // loading
                this.listLoading = true;

                // 
                this.$refs[formName].validate((valid) => {
                    // 
                    if (valid) {
                        addBanner(qs.stringify(this.addBannerData)).then(() => {
                            this.$message({
                                message: "",
                                type: "success"
                            });

                            // banner
                            this.addBannerVisible = false;

                            // loading
                            this.listLoading = false;

                            // 
                            this.getBannerList();
                        }).catch({});
                    } else {  // 
                        console.log('error submit!!');
                        return false;
                    }
                });
            },

            /**
             *  api
             *  banner
             */
            //el-upload
            // 
            updateHandleChange(file){
                // console.log("");

                //pushfiles
                this.upload_arg.imgFile.push(file.raw);

                // console.log(this.upload_arg.imgFile);

                // 
                this.updateUpload();
            },

            // img url
            updateUpload (){
                let formData = new FormData();

                formData.append('logoFile', this.upload_arg.imgFile[0]);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                getImgURI(formData, config).then(res => {
                    // console.log(res);

                    this.updateBannerData.img = res.data.data.imgUrl;
                    // console.log(this.updateBannerData.img);
                });
            },
            // banner()
            updateBanner (row) {
                // console.log("banner");

                this.upload_arg.fileList = [];  // img file

                this.updateBannerData = Object.assign({}, row);

                this.updateBanner.bannerId = this.updateBannerData.id
                // console.log(this.updateBanner.bannerId);
            },

            // banner
            updateBannerSubmit (formName) {
                this.listLoading = true;  //loading

                // 
                this.$refs[formName].validate((valid) => {
                    if (valid) {  // 
                        updateBanner(qs.stringify(this.updateBannerData)).then(() => {
                            this.$message({
                                message: "",
                                type: "success"
                            });

                            this.updateBannerVisible = false;  //banner

                            this.listLoading = false;  //loading

                            this.getBannerList();  //
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                        return false;
                    }
                });
            },

            /**
             *  api
             *  banner
             */
            // 
            delBanner (row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    let id = Object.assign({}, row).id;

                    let params = {
                        id: id
                    }

                    // console.log(params);

                    delBanner(qs.stringify(params)).then(() => {
                        this.$message({
                            message: "",
                            type: "success"
                        });

                        this.getBannerList();  // 
                    }).catch({});
                }).catch(() => {});
            },
        },
        // 
        created () {
            this.getBannerList();
        }
    }
</script>

<style scoped>
    .toolbar {
        padding-bottom: 0;
    }

    .tip{
        height: 22px;
        line-height: 22px;
    }

    .tip li{
        display: inline-block;
        font-size: 12px;
        color: #4ba8cc;
        margin-right: 28px;
    }

    .tabimg{
        width: 75px;
    }
</style>