<template>
    <section class="main_cont">
        <el-row class="chart">
            <el-col id="myChart"></el-col>
        </el-row>
    </section>
</template>

<script>
    import {
        findTodayStatus,  // 
    } from '../../api/api.js';

    // 
    let echarts = require('echarts/lib/echarts')
    // 
    require('echarts/lib/chart/bar')
    // title
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')

    export default {
        name: 'hello',
        data() {
            return {

            }
        },
        // dom
        mounted () {
            // 
            this.getTodayStatus();
        },
        methods: {
            /**
             *  api
             *  
             */
            getTodayStatus () {
                // myChart
                let myChart = echarts.init(document.getElementById('myChart'));

                // myChart loading
                myChart.showLoading();

                /**
                 *  newPayOrderAmountCount: 0,  
                 *  newUserCount: 0,  
                 *  newUsedRedeemCount: 0,  
                 *  newPayOrderCount: 0,  
                 *  newMakeRedeemCount: 9  
                 */
                findTodayStatus().then(res => {
                    // console.log(res);

                    // 
                    let statuses = res.data.data;

                    // console.log(statuses);

                    // myChart loading
                    myChart.hideLoading();

                    // 
                    myChart.setOption({
                        title : {
                            text: '',
                            subtext: '',
                            x:'center'
                        },
                        tooltip : {
                            trigger: 'item',
                            formatter: "{a} <br/>{b} : {c} ({d}%)"
                        },
                        legend: {
                            orient: 'vertical',
                            left: 'left',
                            data: ['','','','','']
                        },
                        series : [
                            {
                                name: '',
                                type: 'pie',
                                radius : '55%',
                                center: ['50%', '50%'],
                                data:[
                                    { value: statuses.newPayOrderAmountCount, name:'' },
                                    { value: statuses.newUserCount, name:'' },
                                    { value: statuses.newUsedRedeemCount, name:'' },
                                    { value: statuses.newPayOrderCount, name:'' },
                                    { value: statuses.newMakeRedeemCount, name:'' }
                                ],
                                itemStyle: {
                                    emphasis: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    }
                                }
                            }
                        ]
                    });

                    // 
                    window.onresize = myChart.resize;
                }).catch({});
            },
        }
    }
</script>

<style scoped>
    .chart{
        height: calc(100vh - 110px);
    }

    .chart #myChart{
        height: 100%;
    }
</style>