<template>
    <section class="main_cont">
        <!--  -->
        <el-row>
            <el-form :inline="true">
                <el-col class="toolbar bdr_radiu" :span="24">
                    <el-col :span="22">
                        <el-form-item>
                            <el-button type="primary" @click="addProductVisible = true"></el-button>
                        </el-form-item>
                    </el-col>
                </el-col>
            </el-form>
        </el-row>

        <!--  -->
        <el-row>
            <el-table class="product_list" :data="product_info" border highlight-current-row v-loading="listLoading" height="calc(100vh - 245px)">
                <!--<el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->

                <el-table-column label="" width="120" align="center">
                    <template slot-scope="scope">
      		<img :src="scope.row.img" class="tabimg" />
      	</template>
                </el-table-column>

                <el-table-column prop="id" label="id" width="80" align="center"></el-table-column>
                <el-table-column prop="title" label="" width="120" align="center"></el-table-column>
                <el-table-column prop="createAt" label="" width="110" align="center"></el-table-column>
                <el-table-column prop="month" label="()" width="120" align="center"></el-table-column>
                <el-table-column prop="originalPrice" label="" width="120" align="center"></el-table-column>
                <el-table-column prop="currentPrice" label="" width="120" align="center"></el-table-column>
                <el-table-column prop="desc" label="" width="auto"></el-table-column>

                <el-table-column fixed="right" label="" width="100">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="updateProduct(scope.row)" v-on:click="updateProductVisible = true"></el-button>
                        <el-button type="text" size="small" @click="delProduct(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar f-cb">
                <el-pagination class="f-fr pagination"
                   :current-page.sync='page_arg.page_index'
                   :page-sizes="page_arg.page_sizes"
                   :page-size="page_arg.page_size"
                   :layout="page_arg.layout"
                   :total="page_arg.total"
                   @current-change='handleCurrentChange'
                   @size-change='handleSizeChange'>
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <el-dialog title="" @keyup.enter.native="addProductSubmit('addProductForm')" :close-on-click-modal="false" :visible.sync="addProductVisible" :before-close="handleClose">
            <el-form :model="addProductData" status-icon :rules="addProductRules" ref="addProductForm" label-width="120px">
                <el-form-item label="" prop="title">
                    <el-input v-model="addProductData.title"  placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-upload
                            action="https://jsonplaceholder.typicode.com/posts/"
                            ref='upload'
                            :before-remove="beforeRemove"
                            :on-change="addHandleChange"
                            multiple
                            :limit="upload_arg.limit"
                            :on-exceed="handleExceed"
                            :file-list="upload_arg.fileList">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                </el-form-item>

                <el-form-item label="" prop="img">
                    <el-input v-model="addProductData.img" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="month">
                    <el-input v-model="addProductData.month" placeholder="()" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="originalPrice">
                    <el-input v-model="addProductData.originalPrice" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="currentPrice">
                    <el-input v-model="addProductData.currentPrice" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="addProductData.desc" placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="addProduct" v-on:click="addProductSubmit('addProductForm')"></el-button>
                    <el-button @click="resetForm('addProductForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--  -->
        <el-dialog title="" @keyup.enter.native="updateProductSubmit('updateProductForm')" :close-on-click-modal="false" :visible.sync="updateProductVisible" :before-close="handleClose">
            <el-form :model="updateProductData" status-icon :rules="updateProductRules" ref="updateProductForm" label-width="120px">
                <el-form-item label="bannerId" prop="id">
                    <el-input v-model="updateProductData.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="title">
                    <el-input v-model="updateProductData.title"  placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-upload
                            action="https://jsonplaceholder.typicode.com/posts/"
                            ref='upload'
                            :before-remove="beforeRemove"
                            :on-change="updateHandleChange"
                            multiple
                            :limit="upload_arg.limit"
                            :on-exceed="handleExceed"
                            :file-list="upload_arg.fileList">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                </el-form-item>

                <el-form-item label="" prop="img">
                    <el-input v-model="updateProductData.img" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="month">
                    <el-input v-model="updateProductData.month" placeholder="()" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="originalPrice">
                    <el-input v-model="updateProductData.originalPrice" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="currentPrice">
                    <el-input v-model="updateProductData.currentPrice" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="updateProductData.desc" placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="updateProductSubmit('updateProductForm')"></el-button>
                    <el-button @click="resetForm('updateProductForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import {
        findProductList,  // 
        getImgURI,  // 
        addProduct,  // 
        updateProduct,  // 
        delProduct  // 
    } from '../../api/api.js';

    export default {
        name: 'commodity',

        data() {
            // url
            let validateUrl = (rule, value, callback) => {
                let reg = /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&amp;:/~\+#]*[\w\-\@?^=%&amp;/~\+#])?/;

                if (!reg.test(value)) {
                    return callback(new Error('url'));
                }

                callback();
            };

            // 
            let validateMonth = (rule, value, callback) => {
                let reg = /^(0?[1-9]|1[0-2])$/;

                if (!reg.test(value)) {
                    return callback(new Error('1-12'));
                }

                callback();
            };

            // 
            let validatePrice = (rule, value, callback) => {
                let reg = /^(-)|([1-9]\d*)((\.\d+)?)$/;

                if (!reg.test(value)) {
                    return callback(new Error('0'));
                }

                callback();
            };

            return {
                /**
                 * common
                 */
                listLoading: false,  // lodding
                dialogVisible: false,  // 

                //
                page_arg: {
                    page_index: 1, // 
                    total: 0, // 
                    page_size: 10, // 1
                    page_sizes: [5, 10, 15, 20, 50], //
                    layout: "total, sizes, prev, pager, next, jumper" // 
                },

                // 
                upload_arg: {
                    limit:1,
                    imgFile: [],
                    fileList: []
                },

                /**
                 * 
                 */
                product_info: [],  // 

                /**
                 * 
                 */
                // 
                addProductData: {
                    title: "aaa",  // 
                    img: "https://nei.netease.com/res/images/interface.png?47c471ba67e19d5c5923ce2e9629d000",  // 
                    month: "3",  // ()
                    originalPrice: "12.5",  // 
                    currentPrice: "5",  // 
                    desc: ""  // 
                },

                // 
                addProductRules: {
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    img: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateUrl, trigger: 'blur' }
                    ],
                    month: [
                        { required: true, message: '()', trigger: 'blur' },
                        { validator: validateMonth, trigger: 'blur' }
                    ],
                    originalPrice: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePrice, trigger: 'blur' }
                    ],
                    currentPrice: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePrice, trigger: 'blur' },
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },

                /**
                 * 
                 */
                // 
                updateProductData: {
                    id: "",
                    title: "",  // 
                    img: "",  // 
                    month: "",  // ()
                    originalPrice: "",  // 
                    currentPrice: "",  // 
                    desc: ""  // 
                },

                // 
                updateProductRules: {
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    img: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateUrl, trigger: 'blur' }
                    ],
                    month: [
                        { required: true, message: '()', trigger: 'blur' },
                        { validator: validateMonth, trigger: 'blur' }
                    ],
                    originalPrice: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePrice, trigger: 'blur' }
                    ],
                    currentPrice: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePrice, trigger: 'blur' },
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },

                /**
                 *  (true , false )
                 */
                addProductVisible: false,  // 
                updateProductVisible: false,  // 
            }
        },
        methods: {
            /**
             * common
             */
            //
            handleClose(done) {
                this.$confirm('').then(() => {
                    done();
                }).catch(err => {
                    throw err;
                });
            },
            //
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },

            /**
             *  
             */
            // 
            handleCurrentChange() {
                this.getProductList();  // 
            },
            // 
            handleSizeChange(page_size) {
                // console.log(page_size);

                this.page_arg.page_size = page_size;  // size

                this.getProductList();  // 
            },

            /**
             * el-upload common
             */
            // 
            handleExceed(files, fileList) {
                this.$message.warning(` ${this.upload_arg.limit}  ${files.length}  ${files.length + fileList.length} `);
            },
            // 
            beforeRemove(file) {
                this.addProductData.img = "";
                this.updateProductData.img = "";
                return this.$confirm(` ${ file.name }`);
            },

            /**
             *  api
             *  
             */
            getProductList () {
                //
                let param = {
                    pageNum: this.page_arg.page_index,  // 
                    pageSize: this.page_arg.page_size,  // 
                };

                // 
                findProductList(param).then(res => {
                    // console.log(res.data.data);

                    this.product_info = res.data.data.list;

                    // 
                    this.page_arg.total = res.data.data.total;
                }).catch({});
            },

            /**
             *  api
             *  
             */
            //
            addProduct () {
                console.log("");

                this.upload_arg.fileList = [];  //img file
            },
            //el-upload
            // 
            addHandleChange(file){
                // console.log("");

                //pushfiles
                this.upload_arg.imgFile.push(file.raw);

                console.log(this.upload_arg.imgFile);

                //
                this.addUpload();
            },

            // img url
            addUpload(){
                let formData = new FormData();

                formData.append('logoFile', this.upload_arg.imgFile[0]);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                getImgURI(formData, config).then(res => {
                    // console.log(res);

                    this.addProductData.img = res.data.data.imgUrl;
                });
            },
            // 
            addProductSubmit (formName) {
                this.listLoading = true;  //loading

                //
                this.$refs[formName].validate((valid) => {
                    //
                    if (valid) {
                        addProduct(qs.stringify(this.addProductData)).then(() => {
                            this.$message({
                                message: "",
                                type: "success"
                            });

                            // banner
                            this.addProductVisible = false;

                            // loading
                            this.listLoading = false;

                            // 
                            this.getProductList();
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                    }
                });
            },

            /**
             *  api
             *  
             */
            //el-upload
            // 
            updateHandleChange(file){
                // console.log("");

                // this.upload_arg.fileList = [];  // img file

                //pushfiles
                this.upload_arg.imgFile.push(file.raw);

                // console.log(this.upload_arg.imgFile);

                // 
                this.updateUpload();
            },

            // img url
            updateUpload (){
                let formData = new FormData();

                formData.append('logoFile', this.upload_arg.imgFile[0]);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                getImgURI(formData, config).then(res => {
                    // console.log(res);

                    this.updateProductData.img = res.data.data.imgUrl;
                    // console.log(this.updateBannerData.img);
                });
            },
            // ()
            updateProduct (row) {
                // console.log("");

                this.upload_arg.fileList = [];  // img file

                this.updateProductData = Object.assign({}, row);

                this.updateProduct.id = this.updateProductData.id;
                // console.log(this.updateProduct.id);
            },

            // 
            updateProductSubmit (formName) {
                //loading
                this.listLoading = true;

                // 
                this.$refs[formName].validate((valid) => {
                    // 
                    if (valid) {
                        updateProduct(qs.stringify(this.updateProductData)).then(() => {
                            this.$message({
                                message: "",
                                type: "success"
                            });

                            // banner
                            this.updateProductVisible = false;

                            // loading
                            this.listLoading = false;

                            // 
                            this.getProductList();
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                    }
                });
            },

            /**
             *  api
             *  
             */
            delProduct (row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    let id = Object.assign({}, row).id;

                    let params = {
                        id: id
                    }

                    // 
                    delProduct(qs.stringify(params)).then(() => {
                        this.$message({
                            message: "",
                            type: "success"
                        });

                        // 
                        this.getProductList();
                    }).catch({});
                });
            },
        },
        // 
        created () {
            this.getProductList();
        }
    }
</script>

<style scoped>
    .toolbar {
        padding-bottom: 0;
    }
</style>