import axios from './axios'

/**
 * common
 */
// 
export const getImgURI = params => {
    return  axios({
        url: 'upload',
        method: 'post',
        data: params
    });
};


/**
 *  //
 */
// 
export const Login = params => {
    return  axios({
        url: 'account/login',
        method: 'post',
        data: params
    });
};

// 
export const Logout = () => {
    return  axios({
        url: 'account/logout',
        method: 'get'
    });
};

// 
export const ModifyPassword = params => {
    return  axios({
        url: 'account/updatePassword',
        method: 'post',
        data: params
    });
};

