<template>
    <section class="main_cont">
        <!--  -->
        <el-row>
            <el-form :inline="true">
                <el-col class="toolbar bdr_radiu" :span="24">
                    <el-col :span="22">
                        <el-form-item>
                            <el-button type="primary" @click="addAccountVisible = true"></el-button>
                        </el-form-item>
                    </el-col>
                </el-col>
            </el-form>
        </el-row>

        <!--  -->
        <el-row>
            <el-table class="Account_list" :data="Account_info" border highlight-current-row v-loading="listLoading" height="calc(100vh - 240px)">
                <!--<el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->
                <el-table-column prop="id" label="id" width="80" align="center"></el-table-column>
                <el-table-column prop="username" label="" width="100" align="center"></el-table-column>
                <el-table-column prop="name" label="" width="100" align="center"></el-table-column>
                <el-table-column label="" width="100" align="center">
                    <template slot-scope="scope">
                        {{ scope.row.isSuperAdmin === 0 ? "" : "" }}
                    </template>
                </el-table-column>

                <el-table-column prop="createAt" label="" width="200" align="center"></el-table-column>
                <el-table-column prop="updateAt" label="" width="auto"></el-table-column>

                <el-table-column fixed="right" label="" width="100">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="delAccount(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar f-cb">
                <el-pagination class="f-fr pagination"
                   :current-page.sync='page_arg.page_index'
                   :page-sizes="page_arg.page_sizes"
                   :page-size="page_arg.page_size"
                   :layout="page_arg.layout"
                   :total="page_arg.total"
                   @current-change='handleCurrentChange'
                   @size-change='handleSizeChange'>
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <el-dialog title="" @keyup.enter.native="addAccountSubmit('addAccountForm')" :close-on-click-modal="false" :visible.sync="addAccountVisible" :before-close="handleClose">
            <el-form :model="addAccountData" status-icon :rules="addAccountRules" ref="addAccountForm" label-width="100px">
                <el-form-item label="" prop="name">
                    <el-input v-model="addAccountData.name"  placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="username">
                    <el-input v-model="addAccountData.username"  placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input type="password" v-model="addAccountData.password" placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="addAccountSubmit('addAccountForm')"></el-button>
                    <el-button @click="resetForm('addAccountForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import {
        findAccountList,  // 
        addAccount,  // 
        delAccount  // 
    } from '../../api/api.js';

    export default {
        name: 'admin',

        data() {
            return {
                /**
                 * common
                 */
                listLoading: false,  // lodding
                dialogVisible: false,  // 

                // 
                page_arg: {
                    page_index: 1, // 
                    total: 0, // 
                    page_size: 10, // 1
                    page_sizes: [5, 10, 15, 20, 50], //
                    layout: "total, sizes, prev, pager, next, jumper" // 
                },

                /**
                 * 
                 */
                Account_info: [],  // 

                /**
                 * 
                 */
                // 
                addAccountData: {
                    name: "aa",  // 
                    username: "aa",  // 
                    password: "aa",  // 
                },

                // 
                addAccountRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    username: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },

                /**
                 *  (true , false )
                 */
                addAccountVisible: false,  // 
            }
        },
        methods: {
            /**
             * common
             */
            //
            handleClose(done) {
                this.$confirm('').then(() => {
                    done();
                }).catch(err => {
                    throw err;
                });
            },
            //
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },

            /**
             *  
             */
            // 
            handleCurrentChange() {
                this.getAccountList();  // 
            },
            // 
            handleSizeChange(page_size) {
                // console.log(page_size);

                this.page_arg.page_size = page_size;  // size

                this.getAccountList();  // 
            },

            /**
             *  api
             *  
             */
            getAccountList () {
                //
                let param = {
                    pageNum: this.page_arg.page_index,  // 
                    pageSize: this.page_arg.page_size,  // 
                };

                findAccountList(qs.stringify(param)).then(res => {
                    // console.log(res.data.data);

                    this.Account_info = res.data.data.list;

                    // 
                    this.page_arg.total = res.data.data.total;
                }).catch({});
            },

            /**
             *  api
             *  
             */
            // 
            addAccountSubmit (formName) {
                // loading
                this.listLoading = true;

                // 
                this.$refs[formName].validate((valid) => {
                    //md5
                    this.addAccountData.password = this.md5(this.addAccountData.password);

                    //
                    if (valid) {
                        addAccount(qs.stringify(this.addAccountData)).then(res => {
                            // console.log(res.data);

                            this.$message({
                                message: res.data.msg,
                                type: "success"
                            });

                            // 
                            this.addAccountVisible = false;

                            // loading
                            this.listLoading = false;

                            // 
                            this.getAccountList();
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                        return false;
                    }
                });
            },

            /**
             *  api
             *  
             */
            delAccount (row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    let id = Object.assign({}, row).id;

                    sessionStorage.setItem('params', id);

                    delAccount().then(() => {
                        this.$message({
                            message: "",
                            type: "success"
                        });

                        sessionStorage.removeItem("params");

                        // 
                        this.getAccountList();
                    }).catch({});
                }).catch(() => {});
            },
        },
        // 
        created () {
            // 
            this.getAccountList();
        }
    }
</script>

<style scoped>
    .toolbar {
        padding-bottom: 0;
    }
</style>