<template>
    <section class="main_cont">
        <!--  -->
        <el-row class="toolbar bdr_radiu f-cb">
            <el-col class="f-fl btn_wrap">
                <el-button type="primary" @click="addSingleRedeemCodeVisible = true"></el-button>
            </el-col>
            <el-col class="f-fl btn_wrap">
                <el-button type="primary" @click="addMoreRedeemCodeVisible = true"></el-button>
            </el-col>
            <el-col class="f-fl btn_wrap">
                <el-tooltip effect="dark" content="Excel" placement="top">
                    <el-button type="primary" @click="downloadExcel" :disabled="this.downloadExcelData.excelURI===''">Excel</el-button>
                </el-tooltip>
            </el-col>
            <el-col class="f-fl btn_wrap del_more">
                <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" class="f-fr"></el-button>
            </el-col>
            <el-col class="tip">
                <p><i class="fa fa-info-circle"></i>ExcelExcel</p>
            </el-col>
        </el-row>

        <!--  -->
        <el-row>
            <el-table class="redeem_code_list" :data="redeem_code_info" border highlight-current-row v-loading="listLoading" @selection-change="selsChange" height="calc(100vh - 240px)">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->
                <!--<el-table-column prop="id" label="id" width="80" align="center"></el-table-column>-->
                <el-table-column prop="userMobile" label="" width="150" align="center"></el-table-column>
                <el-table-column prop="code" label="" width="300" align="center"></el-table-column>
                <el-table-column prop="createAt" label="" width="110" align="center"></el-table-column>
                <el-table-column prop="usedAt" label="" width="110" align="center"></el-table-column>

                <el-table-column label="" width="95" align="center">
                    <template slot-scope="scope">
                        {{ scope.row.isUsed === 1 ? "" : "" }}
                    </template>
                </el-table-column>

                <el-table-column prop="memo" label="" width="auto"></el-table-column>

                <el-table-column fixed="right" label="" width="100">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="delRedeemCode(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-row :span="24" class="toolbar f-cb">
                <!--  -->
                <el-col>
                    <el-pagination class="f-fr pagination"
                        :current-page.sync='page_arg.page_index'
                        :page-sizes="page_arg.page_sizes"
                        :page-size="page_arg.page_size"
                        :layout="page_arg.layout"
                        :total="page_arg.total"
                        @current-change='handleCurrentChange'
                        @size-change='handleSizeChange'>
                    </el-pagination>
                </el-col>
            </el-row>
        </el-row>

        <!--  -->
        <el-dialog title="" @keyup.enter.native="addSingleRedeemCodeSubmit('addSingleRedeemCodeForm')" :close-on-click-modal="false" :visible.sync="addSingleRedeemCodeVisible" :before-close="handleClose">
            <el-form :model="addRedeemCodeData" status-icon :rules="addMoreRedeemCodeRule" ref="addSingleRedeemCodeForm" label-width="160px">
                <el-form-item label="()" prop="month">
                    <el-input v-model="addRedeemCodeData.month"  placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="memo">
                    <el-input v-model="addRedeemCodeData.memo" placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="addSingleRedeemCodeSubmit('addSingleRedeemCodeForm')"></el-button>
                    <el-button @click="resetForm('addSingleRedeemCodeForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--  -->
        <el-dialog title="" @keyup.enter.native="addMoreRedeemCodeSortSubmit('addMoreRedeemCodeForm')" :close-on-click-modal="false" :visible.sync="addMoreRedeemCodeVisible" :before-close="handleClose">
            <el-form :model="addRedeemCodeData" status-icon :rules="addMoreRedeemCodeRule" ref="addMoreRedeemCodeForm" label-width="160px">
                <el-form-item label="()" prop="month">
                    <el-input v-model="addRedeemCodeData.month"  placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="number">
                    <el-input v-model="addRedeemCodeData.number" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="memo">
                    <el-input v-model="addRedeemCodeData.memo" placeholder="" clearable></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="addMoreRedeemCodeSortSubmit('addMoreRedeemCodeForm')"></el-button>
                    <el-button @click="resetForm('addMoreRedeemCodeForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import {
        findRedeemCodeList,  // 
        addSingleRedeemCode,  // 
        addMoreRedeemCode,  // 
        removeRedeemCode,  // 
        removeMoreRedeemCode  // 
    } from '../../api/api.js';

    import axios from '../../api/axios'

    export default {
        name: 'redeem_code',

        data() {
            // 
            let validateMonth = (rule, value, callback) => {
                let reg = /^(0?[1-9]|1[0-2])$/;

                if (!reg.test(value)) {
                    return callback(new Error('1-12'));
                }

                callback();
            };

            // 
            let validateNumber = (rule, value, callback) => {
                let reg = /^[1-9]\d?$/;

                if (!reg.test(value)) {
                    return callback(new Error('2'));
                }

                callback();
            };

            return {
                /**
                 * common
                 */
                listLoading: false,  // lodding
                dialogVisible: false,  // 
                sels: [],  //

                // 
                page_arg: {
                    page_index: 1, // 
                    total: 0, // 
                    page_size: 10, // 1
                    page_sizes: [5, 10, 15, 20, 50], //
                    layout: "total, sizes, prev, pager, next, jumper" // 
                },

                /**
                 * 
                 */
                redeem_code_info: [],  // 

                /**
                 * 
                 */
                // 
                addRedeemCodeData: {
                    month: 1,  // 
                    number: "",  // 
                    memo: ""  //
                },

                // 
                addSingleRedeemCodeRule: {
                    month: [
                        { validator: validateMonth, trigger: 'blur' },
                    ],
                },

                // 
                addMoreRedeemCodeRule: {
                    month: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateMonth, trigger: 'blur' }
                    ],
                    number: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateNumber, trigger: 'blur' }
                    ],
                },

                /**
                 * Excel
                 */
                // Excel
                downloadExcelData: {
                    excelURI: "",  // excel
                },

                /**
                 *  
                 */
                addSingleRedeemCodeVisible: false,  //  
                addMoreRedeemCodeVisible: false,  // 
                downloadExcelVisible: false  // Excel
            }
        },
        methods: {
            /**
             * common
             */
            // 
            handleClose(done) {
                this.$confirm('').then(() => {
                    done();
                }).catch(() => {});
            },
            // 
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            // 
            selsChange (sels) {
                // console.log(sels);

                this.sels = sels;
            },

            /**
             *  
             */
            // 
            handleCurrentChange() {
                this.getRedeemCodeList();  // 
            },
            // 
            handleSizeChange(page_size) {
                // console.log(page_size);

                this.page_arg.page_size = page_size;  // size

                this.getRedeemCodeList();  // 
            },

            /**
             *  api
             *  
             */
            // 
            getRedeemCodeList () {
                //
                let param = {
                    pageNum: this.page_arg.page_index,  // 
                    pageSize: this.page_arg.page_size,  // 
                };

                // 
                findRedeemCodeList(qs.stringify(param)).then(res => {
                    // console.log(res.data.data);

                    this.redeem_code_info = res.data.data.list;

                    // 
                    this.page_arg.total = res.data.data.total;
                }).catch({});
            },

            /**
             *  api
             *  
             */
            addSingleRedeemCodeSubmit (formName) {
                //
                this.$refs[formName].validate((valid) => {
                    // 
                    if (valid) {
                        // 
                        let params = {
                            month: this.addRedeemCodeData.month,
                            memo: this.addRedeemCodeData.memo
                        };

                        // 
                        addSingleRedeemCode(qs.stringify(params)).then(() => {
                            // loading
                            this.listLoading = false;

                            // 
                            this.addSingleRedeemCodeVisible = false;

                            this.$message({
                                message: "",
                                type: "success"
                            });

                            // 
                            this.getRedeemCodeList();

                            // data
                            this.addRedeemCodeData.month = 1;
                            this.addRedeemCodeData.memo = "";
                        }).catch({});
                    } else {  // 
                        console.log('error submit!!');
                    }
                });
            },

            /**
             *  api
             *  
             */
            addMoreRedeemCodeSortSubmit (formName) {
                this.listLoading = true;  //loading

                //
                this.$refs[formName].validate((valid) => {
                    //
                    if (valid) {
                        // 
                        addMoreRedeemCode(qs.stringify(this.addRedeemCodeData)).then((res) => {
                            console.log(res);

                            // loading
                            this.listLoading = false;

                            //
                            this.addMoreRedeemCodeVisible = false;

                            this.$message({
                                message: "",
                                type: "success"
                            });

                            // 
                            this.getRedeemCodeList();

                            // data
                            this.addRedeemCodeData.month = 1;
                            this.addRedeemCodeData.memo = "";

                            // data
                            this.downloadExcelData.excelURI = res.data.data.downloadUrl;
                            // console.log(this.downloadExcel.excelURI);
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                    }
                });
            },

            /**
             *  api
             *  Excel
             */
            downloadExcel () {
                console.log("Excel");
                window.location.href = this.downloadExcelData.excelURI;

                // excel
                this.downloadExcelData.excelURI = "";
            },

            /**
             *  api
             *  
             */
            // 
            delRedeemCode (row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    let id = Object.assign({}, row).id;

                    let params = {
                        ids: id
                    }

                    removeRedeemCode(params).then(res => {
                        console.log(res);

                        this.$message({
                            message: res.data.msg,
                            type: "success"
                        });

                        this.getRedeemCodeList();  // 
                    }).catch({});
                }).catch(() => {});
            },
            // 
            batchRemove: function () {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    let params = [], url_arg;

                    for (let i=0; i<this.sels.length; i++){
                        params.push(this.sels[i].id);
                    }

                    url_arg = "?ids=" + params.join('&ids=');

                    sessionStorage.setItem('url_arg', url_arg);

                    removeMoreRedeemCode().then(res => {
                        // console.log(res);

                        this.$message({
                            message: res.data.msg,
                            type: "success"
                        });

                        sessionStorage.removeItem("url_arg");

                        this.getRedeemCodeList();  // 
                    }).catch({});
                }).catch(() => {});;
            }
        },
        // 
        created () {
            this.getRedeemCodeList();
        }
    }
</script>

<style scoped>
    .btn_wrap{
        width: 150px;
    }

    .del_more{
        width: 98px;
    }

    .pagination {
        margin-bottom: 5px;
    }

    .tip{
        margin-top: 10px;
    }

    .tip p{
        font-size: 13px;
        letter-spacing: 1px;
        color: #8a8484;
    }

    .tip i{
        margin-right: 3px;
        transform: rotateZ(20deg);
        color: #c3bfbf;
    }
</style>