<template>
    <section class="main_cont">
        <!--  -->
        <el-row>
            <el-table class="user_list" :data="user_info" border highlight-current-row v-loading="listLoading" height="calc(100vh - 170px)">
                <!--<el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->
                <el-table-column prop="id" label="id" width="80" align="center"></el-table-column>

                <!--<el-table-column label="" width="100" align="center">-->
                    <!--<template slot-scope="scope">-->
                        <!--<img :src="scope.row.avatar" class="tabimg" />-->
                    <!--</template>-->
                <!--</el-table-column>-->

                <el-table-column prop="username" label="" width="80" align="center"></el-table-column>
                <el-table-column prop="nickname" label="" width="115" align="center"></el-table-column>
                <el-table-column prop="mobile" label="" width="115" align="center"></el-table-column>
                <!--<el-table-column prop="sex" label="" width="115" align="center"></el-table-column>-->
                <!--<el-table-column prop="age" label="" width="115" align="center"></el-table-column>-->
                <!--<el-table-column prop="birthday" label="" width="110" align="center"></el-table-column>-->
                <el-table-column prop="createAt" label="" width="110" align="center"></el-table-column>
                <!--<el-table-column prop="expireAt" label="" width="110" align="center"></el-table-column>-->
                <!--<el-table-column prop="updateAt" label="" width="110" align="center"></el-table-column>-->
                <!--<el-table-column prop="realname" label="" width="110" align="center"></el-table-column>-->
                <el-table-column prop="wxNumber" label="" width="80" align="center"></el-table-column>
                <el-table-column prop="loginTime" label="" width="auto"></el-table-column>
                <!--<el-table-column prop="deviceType" label="" width="80" align="center"></el-table-column>-->
                <!--<el-table-column prop="loginIp" label="ip" width="auto"></el-table-column>-->
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar f-cb">
                <el-pagination class="f-fr pagination"
                   :current-page.sync='page_arg.page_index'
                   :page-sizes="page_arg.page_sizes"
                   :page-size="page_arg.page_size"
                   :layout="page_arg.layout"
                   :total="page_arg.total"
                   @current-change='handleCurrentChange'
                   @size-change='handleSizeChange'>
                </el-pagination>
            </el-col>
        </el-row>
    </section>
</template>

<script>
    import {
        findUserList,  // 
    } from '../../api/api.js';

    export default {
        name: 'user',

        data() {
            return {
                /**
                 * common
                 */
                listLoading: false,  // lodding
                dialogVisible: false,  // 

                // 
                page_arg: {
                    page_index: 1, // 
                    total: 0, // 
                    page_size: 5, // 1
                    page_sizes: [5, 10, 15, 20, 50], //
                    layout: "total, sizes, prev, pager, next, jumper" // 
                },

                /**
                 * 
                 */
                user_info: [],  // 
            }
        },
        methods: {
            /**
             *  
             */
            // 
            handleCurrentChange() {
                this.getUserList();  // 
            },
            // 
            handleSizeChange(page_size) {
                // console.log(page_size);

                this.page_arg.page_size = page_size;  // size

                this.getUserList();  // 
            },

            /**
             *  api
             *  
             */
            getUserList () {
                //
                let param = {
                    pageNum: this.page_arg.page_index,  // 
                    pageSize: this.page_arg.page_size,  // 
                };

                // 
                findUserList(param).then(res => {
                    // console.log(res);

                    this.user_info = res.data.data.list;

                    // 
                    this.page_arg.total = res.data.data.total;
                }).catch({});
            }
        },
        // 
        created () {
            // 
            this.getUserList();
        }
    }
</script>

<style scoped>
    .toolbar {
        padding-bottom: 0;
    }
</style>